export default {
  data() {
    return {
      qrCode: [{ id: 0 }, { id: 1 }],
      previousMargin: "100rpx", //前边距，可用于露出前一项的一小部分，接受 px 和 rpx 值
      nextMargin: "100rpx", //后边距，可用于露出后一项的一小部分，接受 px 和 rpx 值
      currentIndex: 0, //swiper当前索引,
      // 控制有消息页面与无消息页面的切换显示
      isBox: false
    };
  },
  methods: {
    swiperBindchange(e) {
      this.currentIndex = e.detail.current;
    },
    // 去首页
    goIndex() {
      uni.switchTab({
        url: '/pages/index/index'
      })
    }
  }
};
